td {
    padding: 0;
    margin: 0;
}

.transactionHistory {
  border-collapse: collapse;
  width: 70%;
  margin: 0 auto;
}

.transactionHistory td,
.transactionHistory th {
  border: 1px solid #ddd;
  padding: 8px;
}

.transactionHistory tr:nth-child(even) {
  background-color: #f2f2f2;
}

.transactionHistory tr:hover {
  background-color: #ddd;
}

.transactionHistory th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04aa6d;
  color: white;
}
